center;\n    align-items: center;\n    row-gap: 16px;\n    border: 2px dashed #6b7280;\n    transition: 0.3s ease all;\n}\n\nlabel {\n    padding: 8px 12px;\n    color: #fff;\n    background-color: #4bb1b1;\n    transition: 0.3s ease all;\n}\n\n.active-dropzone {\n    color: #fff;\n    border-color: #fff;\n    background-color: #4bb1b1;\n}\n\n.active-dropzone  label {\n        background-color: #fff;\n        color: #4bb1b1;\n    }\n\n\n</style>\n"],"sourceRoot":""}]);
